import React, { ReactNode } from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import { IconChevronDownSmall } from 'core/src/icons';

import useDropdown from '../useDropdown';

import styles from './Button.module.scss';

const cd = cssDecorator(styles);

export interface ButtonProps {
  /** Заголовок выпадающего списка */
  label: ReactNode;

  /** Контент выпадающего списка */
  children: ReactNode;

  /** Флаг кликабельности элемента */
  disabled?: boolean;

  /** Флаг выравнивания по правому краю */
  alignRight?: boolean;

  /** ID для тестирования */
  dataTestId?: string;
}

/** Выпадающий список */
const Button: React.FC<ButtonProps> = ({
  children,
  disabled,
  label,
  alignRight,
  dataTestId
}) => {
  const [wrapperRef, dropdownOpen, toggleDropdown] = useDropdown();

  const dropDownClassNames = cd('list', {
    visible: dropdownOpen,
    align_right: alignRight,
    disabled
  });

  return (
    <div className={ styles.wrapper } ref={ wrapperRef } data-test-id={ dataTestId }>
      <button
        className={ cd('button', { open: dropdownOpen, disabled }) }
        type="button"
        disabled={ disabled }
        onClick={ toggleDropdown }
      >
        <span className={ styles.label }>{ label }</span>
        <IconChevronDownSmall />
      </button>
      <div className={ dropDownClassNames }>{ children }</div>
    </div>

  );
};

export default Button;
